.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 800px;
  margin: 0 auto;
}

.form-container, .search-container {
  width: 100%;
  margin-bottom: 20px;
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 5px;
  background-color: #f9f9f9;
}

h2 {
  margin-top: 0;
}

form {
  display: flex;
  flex-direction: column;
}

.subject-input {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}

.subject-input label {
  margin-right: 10px;
  display: flex;
  align-items: center;
}

.subject-input input {
  flex: 1;
  padding: 5px;
}

input[type="text"], select {
  padding: 5px;
  margin-bottom: 10px;
  border: 1px solid #ccc;
  border-radius: 3px;
}

button {
  padding: 10px;
  border: none;
  border-radius: 5px;
  background-color: #007bff;
  color: white;
  cursor: pointer;
}

button:hover {
  background-color: #0056b3;
}

.search-inputs {
  display: flex;
  align-items: center;
  gap: 10px;
}

.results {
  margin-top: 20px;
}

.student-info {
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin-bottom: 10px;
  background-color: #fff;
}
